*,
::after,
::before {
  box-sizing: border-box; }

html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

body,
html {
  width: 100%;
  height: 100%;
  font-family: sans-serif;
  text-align: center;
  font-size: 18px;
  line-height: 20px;
  color: #fff; }
  body #root,
  html #root {
    width: 100%;
    min-height: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #07bbee;
    background: linear-gradient(to bottom, #07bbee, #05dcc5); }

.btn {
  width: 20px;
  height: 20px;
  padding: 0;
  cursor: pointer;
  transition: 100ms all ease;
  box-sizing: border-box;
  outline: none;
  background: transparent;
  color: #fff;
  border: none;
  font-size: 18px; }
  .btn:hover {
    color: #aaaaaa; }
  .btn:disabled, .btn:hover:disabled {
    color: #586369; }



